* {
  font-family: 'Libre Franklin', sans-serif;
}

body {
  margin: 7.5%;
}

a {
  color: #6f65e9;
}

hr {
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
  margin-right: 1rem;
}

/* img {
  width: 100%;
} */

form {
  text-align: center;
}

label {
  height: 5.5rem;
  font-size:3rem;
}

input {
  height: 5.5rem;
  font-size:3rem;
  color: #6f65e9;
  border-radius: 1rem;
  border-color: #6f65e9;
  padding-left: 1rem;
}

textarea{
  height: 5.5rem;
  font-size:3rem;
  color: #6f65e9;
  border-radius: 1rem;
  border-color: #6f65e9;
  padding-left: 1rem;
  width: 45%;
  height: 35%;
}

button {
  height: 5.5rem;
  font-size: 3rem;
  margin: 1.5rem;
  background: transparent;
  color: #6f65e9;
  border-radius: 1rem;
  border-color: #6f65e9;
}

button:hover{
  color: white;
  background: -webkit-linear-gradient(#6f65e9, #6f65e9);
}

.source-credit {
  font-style: italic;
  font-size: 70%;
}

#annotation-marker {
  position: absolute;
  z-index: 500;
  border: 5px solid #39ff14;
  border-radius: 100px;
  height:100px;
  width:100px;
}

.guest-credentials{
  color: #6f65e9;
  font-size: 2rem;
}

.login:hover{
  text-decoration: underline;
}

/* ——————————————————————— */
