/* DETAIL CONTAINER STYLING */
/* ——————————————————————— */
/* ——————————————————————— */

.div--detail-container{
  /* max-height: 200px; */
  max-width: 100%;

}

  .div--art-annotations-container{
    height: 72rem; /* This controls the height of the annotation list */
    display:-ms-flexbox;
    display:flex;
  }

    .annotation-zone{
      float: left;
      -ms-flex-positive: 1;
          flex-grow: 1;
      margin-right: 3rem;
    }

    .annotation-zone img{
      width: 55rem;
    }

    .div--annotation-list-container{
      overflow-y: scroll;
      width: 100%;

    }

      .div--detail-form{
        width: 30%;
      }

      .div--annotation-card{
        padding-left: 1.5rem;
        padding-right: 1.5rem;

        /* width: 100%; */
        width: 80%;
        /* width:95%; */
        border-radius: 1rem;
        margin-bottom: 1.5%;
        border: 2px solid #6f65e9;
      }

      .annotation-creator{
        color:gray;
        text-align: right;
        font-style: italic;
      }

        .button--annotation-card {
          height: 2rem;
          font-size: 1rem;
        }
          .button--annotation-card-delete {
            color: red;
            border-color: red;
          }
            .button--annotation-card-delete:hover {
              color: white;
              background: red;
            }

          .button--annotation-card-update {
            color: #ffc900;
            border-color: #ffc900;
          }

          .button--annotation-card-update:hover {
            color: white;
            background: #ffc900;
          }
.div--full-annotation-container {
  margin-top: 8.5rem;
  width: 80%;
}

.h1-annotation-list-container-title {
  font-size: 2rem;
  margin-top: 0rem;
}

.div--detail-form{
  text-align: left;
  width: 50%;
}

  .detail-form-small-inputs{
    display: -ms-flexbox;
    display: flex;
    margin-bottom: -2.0rem;
  }
    .detail-form-input{
      margin-top: 1rem;
      margin-bottom: 1rem;
      height: 2rem;
      font-size: 1.5rem;
      margin-right: 1.5rem;
    }

    .detail-form-label{
      margin-top: 1rem;
      font-size: 1.5rem;
      vertical-align: center;
      margin-right: 0.75rem;
    }

    .detail-form-textarea {
        text-align: left;

    }

  .detail-form-button  {
    margin-left: 0rem;
    margin-top: 1rem;
    height: 2.75rem;
    font-size: 1.5rem;
    /* padding: 1rem; */
  }



  .detail-form-textarea  {
    height: 2rem;
    margin-top: .5rem;
    font-size: 1.5rem;
    width: 100%;
    height: 5rem;
  }

  .div--artwork-info button {
    /* margin-left: 0rem;
    margin-top: 1rem; */
  }

