/* ——————————————————————— */
/* ——————————————————————— */
/* NAVBAR STYLING */
/* ——————————————————————— */
/* ——————————————————————— */
.div--nav-bar {
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  background-color: cyan;
  width: 100%;
  height: 5em;
  background-image: -webkit-linear-gradient(315deg, #6f65e9 0%, #667eea 100%);
  background-image: -o-linear-gradient(315deg, #6f65e9 0%, #667eea 100%);
  background-image: linear-gradient(135deg, #6f65e9 0%, #667eea 100%);
  /* background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%); */
}
  .div--nav-bar-item {
    color: white;
    text-decoration: none;
    font-size: 2.5rem;
    vertical-align:center;
    padding: 0.5rem;
    margin: 1rem;
    margin-right: 1.5rem;
    text-transform: uppercase;
    float: left;
    font-size: 2.25rem;
    font-family: 'Libre Franklin', sans-serif;
    font-weight: 200;
  }
    .div--nav-bar-item:hover {
      text-decoration: line-through;
    }

    .logo {
      float: left;
      /* font-weight: 500; */
      font-weight: 600;
    }
    .nav-right {
      float: right;
    }

    .button-logout  {
      background:none;
      border:none;
      margin:0;
      padding:0;
      margin-right: 1.5rem;
      font-weight: 600;
    }

    .button-logout:hover  {
      background:none;
      border:none;
    }



